@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat", "Arial", sans-serif;
  font-display: swap;
  src: url("../fonts/montserratregular.woff2") format("woff2"), url("../fonts/montserratregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat", "Arial", sans-serif;
  font-display: swap;
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat", "Arial", sans-serif;
  font-display: swap;
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat", "Arial", sans-serif;
  font-display: swap;
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023.5px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767.5px) {
  .container {
    padding: 0 10px;
  }
}

.head-container {
  color: #f9fbfd;
  background-color: #011c40;
  background-image: url("../img/main-back-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023.5px) {
  .head-container {
    background-image: url("../img/main-back-tablet.jpg");
  }
}

.main-header {
  position: relative;
}

.main-header__container {
  margin: 0 auto;
  max-width: 1200px;
  width: 85%;
}
@media (max-width: 1023.5px) {
  .main-header__container {
    width: 88.3%;
  }
}
@media (max-width: 767.5px) {
  .main-header__container {
    width: 90.6%;
  }
}

.main-header__wrapper {
  display: flex;
  padding: 32px 0;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1023.5px) {
  .main-header__wrapper {
    padding: 25px 0;
  }
}
@media (max-width: 1023.5px) {
  .main-header__wrapper {
    padding: 20px 0;
  }
}

.main-header__logo svg {
  display: block;
  fill: #f9fbfd;
}
@media (max-width: 1023.5px) {
  .main-header__logo svg {
    width: 38px;
    height: 20px;
  }
}

@media (max-width: 767.5px) {
  .main-header--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 9px;
    left: 3px;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 5px 0 0 #f9fbfd, 0 10px 0 0 #f9fbfd;
  }
}
@media (max-width: 767.5px) and (max-width: 767.5px) {
  .main-header--closed .main-nav__item {
    display: none;
  }
}
@media (max-width: 767.5px) {
  .main-header--opened .main-nav__toggle {
    width: 30px;
    height: 30px;
    top: 16px;
    right: 5px;
  }
}
@media (max-width: 767.5px) {
  .main-header--opened .main-nav__toggle::before,
.main-header--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 13px;
    left: 8px;
    width: 15px;
    height: 2px;
    z-index: 2;
    background-color: #011c40;
  }
}
@media (max-width: 767.5px) {
  .main-header--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
}
@media (max-width: 767.5px) {
  .main-header--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
    box-shadow: none;
  }
}
@media (max-width: 767.5px) {
  .main-header--opened .main-nav__wrapper {
    position: absolute;
    padding: 60px 40px 60px 2vw;
    top: 0;
    right: 0;
    z-index: 10;
    width: 89vw;
    min-height: 568px;
    background-color: #f9fbfd;
  }
}
@media (max-width: 767.5px) {
  .main-header--opened .main-header__logo {
    z-index: 12;
    margin-left: 10vw;
  }
  .main-header--opened .main-header__logo svg {
    fill: #011c40;
  }
}
@media (max-width: 767.5px) {
  .main-header--nojs .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 767.5px) {
  .main-header--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
}
@media (max-width: 767.5px) {
  .main-header--nojs .main-nav__item {
    display: block;
  }
}
@media (max-width: 767.5px) {
  .main-header--opened .intro__wrapper {
    padding-left: 10vw;
  }
}
.main-nav {
  width: 51.2%;
}
@media (max-width: 1023.5px) {
  .main-nav {
    width: 74.2%;
  }
}
@media (max-width: 767.5px) {
  .main-nav {
    width: 89%;
  }
}

.main-nav__toggle {
  display: none;
}
@media (max-width: 767.5px) {
  .main-nav__toggle {
    position: absolute;
    padding: 0;
    top: 9px;
    right: 12px;
    z-index: 11;
    display: block;
    width: 30px;
    height: 30px;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    cursor: pointer;
  }
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767.5px) {
  .main-nav__list {
    flex-direction: column;
  }
}

.main-nav__list a {
  font-weight: 500;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023.5px) {
  .main-nav__list a {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767.5px) {
  .main-nav__list a {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}

.main-nav__item {
  padding: 10px 0;
}
@media (max-width: 767.5px) {
  .main-nav__item {
    padding: 8px 45px 8px 10px;
    margin-bottom: 10px;
  }
}

.intro h1 {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
}
@media (max-width: 1023.5px) {
  .intro h1 {
    margin-bottom: 29px;
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767.5px) {
  .intro h1 {
    margin-bottom: 25px;
    font-size: 36px;
    line-height: 45px;
  }
}

.intro p {
  margin: 0;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023.5px) {
  .intro p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767.5px) {
  .intro p {
    width: 70%;
    font-size: 16px;
  }
}

.intro__wrapper {
  padding: 283px 13.2%;
}
@media (max-width: 1023.5px) {
  .intro__wrapper {
    padding: 280px 6%;
  }
}
@media (max-width: 767.5px) {
  .intro__wrapper {
    padding: 185px 2.7%;
  }
}

.preamble {
  padding: 120px 0 145px;
}
@media (max-width: 1023.5px) {
  .preamble {
    padding: 100px 0;
  }
}
@media (max-width: 767.5px) {
  .preamble {
    padding: 65px 0 85px;
  }
}

.preamble__wrapper {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767.5px) {
  .preamble__wrapper {
    flex-direction: column;
  }
}

.preamble__text-wrapper {
  width: 40%;
}
@media (max-width: 1023.5px) {
  .preamble__text-wrapper {
    width: 47.8%;
  }
}
@media (max-width: 767.5px) {
  .preamble__text-wrapper {
    width: 100%;
    margin-bottom: 50px;
  }
}

.preamble__img-wrapper {
  width: 48.3%;
}
@media (max-width: 1023.5px) {
  .preamble__img-wrapper {
    margin-right: 5px;
    width: 47.8%;
    height: 66%;
  }
}
@media (max-width: 767.5px) {
  .preamble__img-wrapper {
    width: 100%;
    max-height: 156px;
    margin: 0;
  }
}

.preamble img {
  object-fit: cover;
}
@media (max-width: 767.5px) {
  .preamble img {
    height: 156px;
  }
}

.preamble h2 {
  margin: 66px 0 40px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
@media (max-width: 1023.5px) {
  .preamble h2 {
    margin: 0 0 30px;
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767.5px) {
  .preamble h2 {
    margin: 0 0 30px;
    font-size: 24px;
    line-height: 34px;
  }
}

.preamble p {
  margin: 0;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767.5px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767.5px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767.5px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

